import React from "react";
import { Link } from "react-router-dom";

const Navbar = (props) => {
  return (
    <>
      <li className="nav-item">
        <Link style={{textAlign:'center',color:'white',fontSize:'1.3em'}}  className=" nav-link" to={"/" + props.source}>
          {props.source}
        </Link>
      </li>
    </>
  );
};

export default Navbar;
