import "./cardPortfolio.css";
import { SecondaryButton } from "../buttons/button/button";

const CardPortfolio = (props) => {
  return (

    <div className="CardPortfolio">
  <a className="linkCardPortfolio" href={props.href}>
    <img className="imageCardPortfolio" src={props.src} alt="" />
  </a>
  <div className="nameCardPortfolio">{props.name}</div>
  <div className="centerbuttonPortfolio">
    <SecondaryButton text="view project" href={props.href} />
  </div>
</div>

);
};

export default CardPortfolio;
