import "./style.css";
import logo from '../../assets/images/logo.png'
import Navbar from "../../components/navbar";
import { Link } from "react-router-dom";
// import logo from '../../assets/images/banner-bg.jpg'
const Header = () => {
  return (
    <div>
      <nav className="navbar ss navbar-expand-lg  fixed-top ">
        <div className="container">
          {/* <Link className="navbar-brand mx-auto d-lg-none" to="/home">
           
            <strong className="d-block"><img src={logo} alt='er'/></strong>
          </Link> */}

          <button
            className="navbar-toggler navbar-toggler-color"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarNav"
            aria-controls="navbarNav"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <div className="navbar-toggler-icon toggleIconHeader"></div>
          </button>

          <div className="collapse navbar-collapse" id="navbarNav">
            <ul className="navbar-nav mx-auto">
            {/* logo */}
              {/* <Link className=" navbar-brand d-none d-lg-block" to='/home'>
                <img src={logo} alt="image1"/>
              </Link> */}
              <Navbar source='home'/>
              <Navbar source='aboute'/>
              <Navbar source='Portfolio'/>
              <Navbar source='contact'/>
            </ul>
          </div>
        </div>
      </nav>
    </div>
  );
};

export default Header;
