import CardPortfolio from "../../components/cardPortfolio/cardPortfolio";
import SectionHeader from "../../components/sectionHeader/sectionHeader";
import "./portfolio.css";
import portfoliodata from "../../data/portfoliodata";
import { useState } from "react";
import { useEffect } from "react";
const Portfolio = () => {
  let [type, setType] = useState("");

  // 1
  let allProjects = portfoliodata.map((card) => {
    return (
      <CardPortfolio
        key={card.id}
        name={card.name}
        src={card.image}
        href={card.href}
      />
    );
  });

  useEffect(() => {
    setType(allProjects);
  }, []);

  // 2
  let fontendMentor = portfoliodata.map((card) => {
    if (card.type === "c") {
      return (
        <CardPortfolio
          key={card.id}
          name={card.name}
          src={card.image}
          href={card.href}
        />
      );
    }
  });
  // 3
  let spidertab = portfoliodata.map((card) => {
    if (card.type === "m") {
      return (
        <CardPortfolio
          key={card.id}
          name={card.name}
          src={card.image}
          href={card.href}
        />
      );
    }
  });

  let checke = (type) => {
    // refresh for cards
    setType("");

    if (type === "all") {
      setType(allProjects);
    } else if (type === "fontendMentor") {
      setType(fontendMentor);
    } else if (type === "spidertab") {
      setType(spidertab);
    } else {
      return null;
    }
  };
  return (
    <>
      <div className="my-portfolio">
        <SectionHeader header="portfolio" />

        <div className="allshowProjects">
          <button
            className="showProjects"
            onClick={() => {
              checke("all");
            }}
          >
            all project
          </button>
          <button
            className="showProjects"
            onClick={() => {
              checke("fontendMentor");
            }}
          >
            fontend mentor
          </button>
          <button
            className="showProjects"
            onClick={() => {
              checke("spidertab");
            }}
          >
            spider tab
          </button>
        </div>
              <span>one</span>
              <span>one</span>
              <span>one</span>
              <span>one</span>
        <div className="allProjectsPortfolio">{type}</div>
      </div>
    </>
  );
};

export default Portfolio;
