import { useState } from "react";
import "./fadeIn.css";
import { useEffect } from "react";

const FadeIn = () => {
let [type, setType] = useState('flex')


  window.addEventListener('load', ()=>{

    setType('none')
  })



    

    return (
<>
     
<div className="fadeIn" style={{display:type}}>
      <div className="oneitemfadeIn1 oneitemfadeIn"></div>
      <div className="oneitemfadeIn2 oneitemfadeIn"></div>
      <div className="oneitemfadeIn3 oneitemfadeIn"></div>
      <div className="sircelfadin"></div>
    </div></>
  );
};

export default FadeIn;
